.services {
  color: $text;
  background-color: $darkBackground;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: start;
  padding-top: 65px;
  padding-bottom: 127px;

  @media (max-width: $desktop) {
    padding-top: 23px;
    padding-bottom: 63px;
  }

  @media (max-width: $tablet) {
    padding: 25px 0;
    padding-bottom: 52px;
  }
}

.services h2 {
  letter-spacing: 0.8px;
  font-weight: 300;
  font-size: 35px;
  margin-bottom: 37px;

  @media (max-width: $desktop) {
    letter-spacing: 0.7px;
    font-size: 31px;
    width: 620px;
    line-height: 45px;
    margin-bottom: 14px;
  }

  @media (max-width: $tablet) {
    font-size: 28px;
    width: auto;
    line-height: 41px;
    letter-spacing: 0.4px;
    margin-bottom: 17px;
    margin-left: 7px;
  }
}

.services__list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;

  @media (max-width: $desktop) {
    flex-direction: column;
    flex-wrap: wrap;
  }
}

.services__item {
  display: flex;
  box-sizing: border-box;
  width: 31.6%;
  height: 640px;
  padding-top: 20px;

  @media (max-width: $desktop) {
    margin: 13px 0;
    width: 100%;
    height: 278px;
  }

  @media (max-width: $desktop) {
    background-repeat: no-repeat;
    background-size: 100%;
    background-position: 0 50%;
  }


  @media (max-width: $tablet) {
    margin-top: 0;
    margin-bottom: 9px;
  }

  picture {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;

    &:hover {
      background-color: rgba(255, 255, 255, 0.3);
    }

    img {
      width: 100%;
      height: 100%;
    }
  }

  h3 {
    letter-spacing: 0.3px;
    font-weight: 300;
    margin: 0;
    width: 170px;
    margin-top: 5px;
    font-family: "Roboto Condensed", "Arial", sans-serif;
    font-size: 20px;
    line-height: 30px;

    @media (max-width: $desktop) {
      margin: 0;
      line-height: 29px;
      width: 170px;
    }
  }

  a {
    position: relative;
    text-decoration: none;
    color: $text;
    padding: 44px;
    display: block;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    background-color: rgba(27, 45, 55, 0.5);

    @media (max-width: $tablet) {
      padding: 23px 33px;
    }

    &:hover {
      background-color: rgba(27, 45, 55, 0.9);
    }

    &::before {
      content: "";
      box-sizing: border-box;
      padding: 20px;
      position: absolute;
      width: 63px;
      height: 63px;
      background-color: $blueBackground;
      background-image: url("data:image/svg+xml,%3Csvg width='6' height='9' viewBox='0 0 6 9' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 8.5L5 4.5L1 0.5' stroke='white' stroke-linejoin='round'/%3E%3C/svg%3E");
      background-repeat: no-repeat;
      background-position: 50% 50%;
      right: 0;
      bottom: 0;
    }
  }
}
